html {
  scroll-behavior: smooth;
}

body {
  min-height: 110vh;
  background-color: #93F95D;
}

li {
  color: #000;
}

.skip-link {
  position: absolute;
  top: -100px;
  left: 0;
  transition: top 0.25s;
  background-color: red;
  color: #fff;
  padding: 0.5rem 1rem;
  border: 1px solid #000;
  transition: top 1s;
}

.skip-link:focus {
  top: 0;
  transition: top 0.1s;
}

.error {
  color: #90040B;
  font-size: 0.8rem;
  margin: 0;
}